.App {
  text-align: center;
  max-width: 60%;
  margin: 0 auto;
}

.App header {
  background-color: #083282;
  height: 10vh;
  min-height: 50px;
  color: white;
  width: 100%;
  max-width: 100%;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;
  background-color: #252525;
}

.scroll {
  padding: 10px;
  height: 50vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

.scroll::-webkit-scrollbar {
  width: 0.25rem;
}

.scroll::-webkit-scrollbar-track {
  background: #1e1e24;
}

.scroll::-webkit-scrollbar-thumb {
  background: #6649b8;
}

form {
  height: 100%;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  max-width: 100%;
  display: flex;
  font-size: 1.5rem;
}

form button {
  width: 20%;
  background-color: rgb(56, 56, 143);
}

input {
  line-height: 1.5;
  width: 100%;
  font-size: 1.5rem;
  background: rgb(58, 58, 58);
  color: white;
  outline: none;
  border: none;
  padding: 0 10px;
}

button {
  background-color: #083282;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.sign-in {
  color: #ffffff;
  max-width: 400px;
  margin: 0 auto;
  background-color: #252525;
}

.sign-out{
  color: #ffffff;
  background-color: #083282;
}

.sign-out:hover{
  color: #ff0000;
  background-color: #083282;
}

form button {
  width: 20%;
  background-color: rgb(56, 56, 143);
  display: flex;
  align-items: center;
  justify-content: center;
}

ul,
li {
  text-align: left;
  list-style: none;
}

.conteudo-mensagem{
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

.profilepic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}